<script land="script" scoped>
import SearchBox from '@/components/search/search-box'

export default {
  name: 'MhMore',
  components: {
    SearchBox
  },
  data() {
    return {
      show: false,
      styleBoolen: true,
      style: '',
      mylist: [],
      gyslist: [],
      mytemplist: [],
      gystemplist: [],
      headerData: {
        title: '项目详情',
        leftUrl: '',
        leftText: ' '
      }
    }
  },
  created() {
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconxitonggonggao1', 'title': '我的待办', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'icontongzhigonggao', 'title': '我的项目', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconshanchu2', 'title': '系统公告', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconxitonggonggao1', 'title': '电子竞价', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconbanshizhinan', 'title': '网上商城', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mytemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconbanshizhinan', 'title': '网上商城', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.mylist.push(this.mytemplist)
    this.gystemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconxitonggonggao1', 'title': '测试1', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.gystemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'icontongzhigonggao', 'title': '测试2', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.gystemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconshanchu2', 'title': '测试3', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.gystemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconxitonggonggao1', 'title': '测试4', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.gystemplist.push({ 'component': 'MhMore', 'hidden': false, 'icon': 'iconbanshizhinan', 'title': '测试5', 'name': 'more', 'needLogin': false, 'opened': false, 'path': 'MhMore', 'showMessage': false })
    this.gyslist.push(this.gystemplist)
  },
  methods: {
    edit() {
      if (this.styleBoolen === true) {
        this.style = 'function-edit'
        this.show = !this.show
      } else {
        this.style = ''
        this.show = !this.show
      }
      this.styleBoolen = !this.styleBoolen
    },
    add(index) {
      if (this.style === '') {
        return false
      }
      if (this.mytemplist.length === 8) {
        alert('我的应用已满')
        return false
      }
      if (this.styleBoolen === false) {
        this.mytemplist.push(this.gystemplist[index])
        this.gystemplist.splice(index, 1)
      }
    },
    del(index) {
      if (this.style === '') {
        return false
      }
      this.gystemplist.push(this.mytemplist[index])
      this.mytemplist.splice(index, 1)
    },
    onQueryChange() {

    },
    goSearch() {

    },
    over() {
      this.styleBoolen = !this.styleBoolen
      this.style = ''
      this.show = !this.show
    }
  }
}
</script>

